// В этом файле должны быть стили для БЭМ-блока b-country-select, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.b-country-select {

  $block-name:                &; // #{$block-name}__element

  margin-bottom: 15px;

  label {
    display: inline-block;
    margin: 2px 0;
    color: #000;
  }

  .inside {
    overflow: visible;
  }

  &__list {
    width: 100%;
    max-height: 200px;
    margin: 0;
    padding: 5px 0;
    overflow-x: hidden;
    overflow-y: auto;

    & > li:first-child {
      display: none;
    }
  }
}



.country-select {
  display: block;
  position: relative;

  input {
    padding-left: 42px !important;
    padding-right: 42px !important;

    &::-ms-clear {
      display: none;
    }
  }

  .divider {
    padding-bottom: 5px;
    margin-bottom: 5px;
    border-bottom: 1px solid #c4c4c4;
  }

  .selected-flag {
    position: absolute;
    top: 1px;
    left: 1px;
    z-index: 1;
    width: calc(100% - 2px);
    height: 40px;
    padding: 0 0 0 14px;
    border-radius: 3px;
    cursor: pointer;
  }

  .selected-flag .flag {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
  }

  .selected-flag .arrow {
    position:absolute;
    top: 50%;
    margin-top: -2px;
    right: 18px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 5px 5px 0 5px;
    border-color: #c4c4c4 transparent transparent transparent;
    // transition: all 0.3s;
  }

  .selected-flag .arrow.up {
    // transform: rotate(180deg);
  }

  .country-list {
    position: absolute;
    left: 0;
    right: 0;
    top: calc(100% - 1px);
    z-index: 2;
    list-style: none;
    text-align: left;
    padding: 3px 0;
    margin: 0;
    background-color: white;
    border: 1px solid #c4c4c4;
    white-space: nowrap;
    width: 100%;
    max-height: 200px;
    overflow-x: hidden;
    overflow-y: auto;
  }

  .country {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 8px 14px;
    line-height: 1;
    color: inherit;
    text-decoration: none;
    cursor: pointer;

    &.active,
    &.highlight {
      background-color: $gray-lightest-2;
    }
  }

  .flag {
    flex-shrink: 0;
    box-shadow: 0 0 0 1px $gray-lightest-2;
  }

  .country-name {
    display: block;
    padding-left: 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

